import store from "@/store";

// Remove all logic except authentication check
export function beforeRouteEnter(to, from, next) {
  const currentUser = store.state.auth.user;

  if (!currentUser) {
    next("/login");
    return;
  }

  // Do not call any Vuex getters or dispatches here.
  // Just proceed to the route.
  next();
}
